@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

.module {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  height: 100vh;
  margin: auto;
  text-align: center;
  padding: 4rem;
}
.module__image img {
  object-fit: cover;
  width: 100%;
}
.module__container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-top: 2rem;
}
.module__text {
  margin-top: 2rem;
  line-height: 2.4rem;
  color: hsl(0deg, 0%, 7%);
}
.module__title {
  font-size: 3rem;
  color: hsl(0deg, 0%, 7%);
}
.module__box {
  margin-top: 3rem;
}
.module__button {
  background-color: hsl(21deg, 50%, 79%);
  color: hsl(0deg, 0%, 100%);
  font-weight: 700;
  padding: 1rem 3rem;
  border-radius: 3.2rem;
  border: none;
}
.module__button:hover {
  cursor: pointer;
  background-color: rgb(240, 213, 200);
}

@media (min-width: 992px) {
  .module {
    max-width: 1440px;
    flex-direction: row;
    justify-content: center;
    text-align: left;
    height: 40vh;
    margin: 9.6rem auto;
    padding: 0;
  }
  .module__container {
    margin: 0;
    flex-basis: 35%;
    flex-grow: 0;
    margin-left: 4rem;
    justify-content: center;
  }
  .module__box {
    flex-grow: 0;
  }
  .module__image {
    flex-basis: 35%;
    font-size: 0;
    height: 100%;
    margin-right: 4rem;
  }
}